<template>
    <div class="user-info fl">
        <img class="user-icon" src="../assets/header-icon.png"/>
        <p class="program-name">小程序名：{{programName}}</p>
        <p class="program-limit">授权时间: {{programData}}天</p>
        <p class="program-status " v-if="programStatusFlag">已授权</p>
        <a class="program-status " v-else href="javascript:;" v-on:click="doAuthorize()">去授权</a>
        <br>
        <a href="javascript:;" v-bind:class="{ 'disabled': !programStatusFlag}" v-on:click="doAuthorize()">进入管理后台</a>
    </div>
</template>
<script>
    module.exports = {

        data: function () {
            return {
                programName: "--",
                programStatusFlag:  false,
                programStatus:  '接口未授权',
                programData: 0,
            }
        },
        created: function () {
            var that = this;
            that.$http.post('/user/status/',that.$data).then(function(res){
                var dt = res.data.data,
                    wechat_permit = dt.wechat_permit;
                if(wechat_permit) {

                }
                console.log(dt);
                if(!dt.login) { //un login
                    location.href = '#login';
                }
            },function(error){
                //error
            });
        },
        methods: {
            doAuthorize: function () {
                layer.confirm('即将前往公众号授权页面，确认授权吗？',{title:'说明'},function () {
                    window.location.href = '/wechat/bind/minapp/';
                });
            },

        }
    }
</script>

<style scoped>
    .user-info {background: #FFF;width: 250px;text-align: center;padding-bottom: 40px;}
    .user-icon {margin: 30px 0 20px;}
    .program-name {font-weight: bold;}
    .program-limit {color: #b6b6b6;}
    .program-status {margin: 20px 0;color: #54a427; border: 1px solid #54a427;border-radius: 5px;width: 110px;line-height: 35px;display: inline-block;}
    .user-info a:last-child {background-color: #54a427; color: #FFF;border-radius: 5px;border: 1px solid #54a427;width: 110px;line-height: 35px;display: inline-block;}
</style>



