<template>
    <div class="main-area">
        <div class="setting-header">
            <a href="javascript:;">小程序首页</a>
            <a href="javascript:;">> 综合设置</a>
        </div>
        <div class="setting-area-item">
            <h3>广告位设置</h3>
            <a href="javascript:;" class="wk-btn fr">添加头部广告图</a>
            <div class="">
                <a href="javascript:;"></a>
                <a href="javascript:;"></a>
                <a href="javascript:;"></a>
                <a href="javascript:;"></a>
            </div>
            <p>广告图最多只可上传4张，点击可选择对应跳转的小程序内容链接。</p>
            <a href="javascript:;" class="wk-btn fr">更新广告设置</a>
        </div>
    </div>
</template>
<script>
    module.exports = {

        data: function () {
            return {
                codeFlag: false,
                programName: '--',
                programStatusFlag:  false,
                programStatus:  '尚未提交审核', //等待审核中
                programTime: '2017-06-05',
                loginMsg: "请用您的用户"
            }
        },
        methods: {
            changeItme: function (type) {
                console.log(type);
                type == 1 ? this.$set('codeFlag',true) : this.$set('codeFlag',false);
            }
        }
    }
</script>

<style scoped>
    .setting-header {background: #FFF; padding: 0 20px;overflow: hidden; line-height: 30px;}
    .setting-header a {color: #b5b5b5 !important;}
</style>



